window.addEventListener('load', function () {
    
    const tab = (btnSel, bodySel) => {
        const tabBtn = document.querySelectorAll(btnSel);
        const tabBody = document.querySelectorAll(bodySel);

        for (let i = 0; i < tabBtn.length; i++) {

            tabBtn[i].addEventListener('click', () => {
    
                for (let x = 0; x < tabBtn.length; x++) {
                    tabBtn[x].classList.remove('active_tab');
                    tabBody[x].classList.remove('active_tab_body');
                }
    
                tabBtn[i].classList.add('active_tab');
                tabBody[i].classList.add('active_tab_body');
            });
        };
    };

    const setClass = (setBtnSel, remBtnSel, itemSel, cls) => {
        const setBtn = document.querySelector(setBtnSel);
        const remBtn = document.querySelector(remBtnSel);
        const item = document.querySelector(itemSel);

        setBtn.addEventListener('click', () => {
            item.classList.add(cls);
        });

        remBtn.addEventListener('click', () => {
            item.classList.remove(cls);
        });
    };

    const toggleSub = (btnSel, itemSel) => {
        const btn = document.querySelector(btnSel);
        const item = document.querySelector(itemSel);

        let tick;

        btn.addEventListener('click', () => {
            item.style.display = 'block';
            clearInterval(tick);
        });

        btn.addEventListener('mouseleave', () => {
            tick = setTimeout(() => {
                item.style.display = 'none'
            }, 1200);
        });

        item.addEventListener('mouseover', () => {
            item.style.display = 'block';
            clearInterval(tick);
        });

        item.addEventListener('mouseleave', () => {
            item.style.display = 'none';
            clearInterval(tick);
        });

        return btn, item;
    };

    // filter tab
    tab('.tab_btn', '.tab_body');
    tab('[data-homeTabBtn]', '[data-homeTabBody]');

    // submenu - services
    toggleSub('#submenu_toggle', '#submenu');
    toggleSub('#submenu', '#submenu');

    // languages dropdown
    toggleSub('.current_lang', '.languages');
    toggleSub('.languages', '.languages');

    // mobile menu
    setClass('.mobileMenuBtn', '.closeMobileMenu', '.wrapper', 'active_wrapper');


    // mobile filter
    try {
        document.querySelector('#openES_filter').addEventListener('click', (e) => {
            e.target.style.marginBottom = "0px";

            document.querySelector('#es_filter').style.cssText = `
                display: block;
                margin-bottom: 25px;
            `;
        });
    } catch {}

    try {
        const inputs = document.querySelectorAll('.a_btns label > input');

        document.querySelectorAll('.a_btns label')[0].style.cssText = `
            background: rgba(23, 99, 222, 0.1);
            border: 1px solid rgba(23, 99, 222, 0.2);
        `

        for (let i = 0; i < inputs.length; i++) {
            inputs[i].addEventListener('change', () => {
            
                for (let x = 0; x < inputs.length; x++) {
                    inputs[x].removeAttribute('checked');
                    inputs[x].parentElement.removeAttribute('style');
                }

                inputs[i].setAttribute('checked', "");

                if (inputs[i].hasAttribute('checked')) {
                    inputs[i].parentElement.style.cssText = `
                        background: rgba(23, 99, 222, 0.1);
                        border: 1px solid rgba(23, 99, 222, 0.2);
                    `
                }
            });
        }

    } catch {}

    try {
        document.querySelector('#toUpFile').addEventListener('click', () => {
            const t = setTimeout(() => {
                document.querySelector('#upFile').click();  
            }, 500);

            if (t !== undefined) {
                clearTimeout(t);
            }
        });
    } catch {}


    // slider

    try {
        new Swiper('.swiper-container', {
            slidesPerView:4,
            spaceBetween: 30,
            loop: true,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            breakpoints: {
                640: {
                  slidesPerView: 1
                },
                768: {
                  slidesPerView: 2
                },
                1024: {
                  slidesPerView: 3
                }
            }
        });
    } catch {}
    
    const a = document.querySelectorAll('a.splide__slide');

    Array.from(a).forEach(btn => {
        btn.addEventListener('click', (e) => {
            e.preventDefault();
        });
    });

    try {

        // Create and mount the thumbnails slider.
        var secondarySlider = new Splide( '#secondary-slider', {
            rewind      : true,
            fixedWidth  : 200,
            speed: 200,
            fixedHeight : 150,
            isNavigation: true,
            gap         : 5,
            pagination  : false,
            cover       : true,
            arrows     : false,
            breakpoints : {
                '420': {
                    fixedWidth  : 100,
                    fixedHeight : 60,
                },
                '520': {
                    fixedWidth  : 160,
                    fixedHeight : 80,
                },
                '810': {
                    fixedWidth  : 200,
                    fixedHeight : 120,
                },
                '1024': {
                    fixedWidth  : 160,
                    fixedHeight : 100,
                }
            }
        } ).mount();

        // Create the main slider.
        var primarySlider = new Splide( '#primary-slider', {
            type       : 'slide',
            heightRatio: 0.65,
            pagination : false,
            arrows     : true,
            cover      : true,
            speed: 200,
        } );

        // Set the thumbnails slider as a sync target and then call mount.
        primarySlider.sync( secondarySlider ).mount();

    } catch (e) {console.log(e)}
});